*,
*::after,
*::before {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

body {
	background: #f5f7fa;
	padding: 10px;
	font-family: 'Montserrat';
	font-size: 11px;
}

img {
	display: block;
	width: 100%;
	height: auto;
}

h1,
h2,
h3,
h4,
h5,
p {
	margin: 0;
}

ul {
	padding: 0;
	list-style: none;
}

.wrapper {
	display: relative;
	background: #ffffff;
	display: flex;
	flex-direction: row;

	max-height: 1120px;
}

.sidebar {
	background: #1e2939;
	width: 380px;
	padding-top: 30px;
}

.contacts-container {
	margin-left: 30px;
	margin-top: 30px;
	margin-right: 30px;
	color: blanchedalmond;
}

.contacts-title {
	font-weight: 700;
	line-height: 24px;
	color: #ffffff;
}

.contact-item > a {
	color: azure;
	text-decoration: none;
}

.techSkills-conteiner,
.softSkills-conteiner {
	padding-left: 30px;
	padding-top: 20px;
	color: white;
}

.allSkills {
	padding: 10px;
	font-weight: bold;
	color: black;
}

.language-conteiner {
	padding-left: 30px;
	padding-top: 20px;
	color: white;
	margin-bottom: 20px;
}

.about-me {
	padding-left: 40px;
	padding-right: 120px;
	padding-top: 20px;
	padding-bottom: 20px;
}

.block-heading {
	margin-top: 20px;
}

.list__item {
	margin-top: 15px;
}

.list__link {
	font-weight: bold;
	text-decoration: none;
	color: inherit;
}

.list__proj__link {
	font-weight: bold;
	text-decoration: none;
	color: inherit;
}
